import React from "react";
import { Container } from "react-bootstrap";
import { v4 } from "uuid";
// needed styles
import styles from "./index.module.scss";
// needed components
import TouristResortCard from "./TouristResortCard/TouristResortCard";
import TouristResortSearchBox from "./TouristResortSearchBox/TouristResortSearchBox";
const TouristResortsPage = () => {
  return (
    <div className={styles["container"]}>
      <Container>
        <h2 className={styles["header"]}>الغردقة: وجدنا 1,066 مكان إقامة</h2>
        <div className={styles["content"]}>
          <p>
            الغردقة هي العاصمة الإدارية لمحافظة البحر الأحمر، وتشغل مساحة 40 كم
            من الشريط الساحلي للشاطئ الغربي للبحر الأحمر. يحدها شمالاً مدينة رأس
            غارب وجنوباً مدينة سفاجا وشرقاً ساحل البحر الأحمر وغرباً جبال البحر
            الأحمر. وتقع بها جزر الجفتون وأم قمر ومجاويش وأبو رمادة وأبو منقار
            والفنادير وشدوان، ومن أشهر منتجعاتها الجونة وسهل حشيش وسوما باي
            ومكادي باي، وتتمثل أحيائها الرئيسية في حي الدهار وحي السقالة وحي
            الأحياء.
          </p>
        </div>
        <div>
          <TouristResortSearchBox />
        </div>
        {[1, 1, 1, 1, 1, 1].map((item) => (
          <TouristResortCard key={v4()} />
        ))}
      </Container>
    </div>
  );
};

export default TouristResortsPage;
