import React from "react";

// styles
import Rate from "rc-rate";
import "rc-rate/assets/index.css";
// needed asssets

// svgs

// images
// needed styles
import styles from "./index.module.scss";

const HeroSection = () => {
  return (
    <div className={styles["card"]}>
      <div className={styles["card-image"]}>
        <img src="https://source.unsplash.com/random?beaches" alt="" />
      </div>
      <div className={styles["card-info"]}>
        <div className={styles["card-info-header"]}>
          <p>SindBad Club</p>
          <p>
            <Rate value={5} className={styles["rate-stars"]} />
          </p>
        </div>
        <div className={styles["card-info-underline"]}>
          <span>الممشي السياحي بالغردقه</span>
        </div>
        <div>
          <p>
            يقع سندباد كلوب على بعد مجرد 15 دقيقة من مطار الغردقة ويشتمل على
            منتزه مائي وسبا متكامل، ويمكن للضيوف الاستمتاع بالدخول مجاناً إلى
            المنتزه المائي. توفر جميع الغرف تكييف الهواء وتلفزيون مع قنوات...
            يقع سندباد كلوب على بعد مجرد 15 دقيقة من مطار الغردقة ويشتمل على
            منتزه مائي وسبا متكامل، ويمكن للضيوف الاستمتاع بالدخول مجاناً إلى
            المنتزه المائي. توفر جميع الغرف تكييف الهواء وتلفزيون مع قنوات...
          </p>
        </div>
      </div>
      <div className={styles["card-action"]}>
        <div className={styles["card-rating"]}>
          <div className={styles["rating-text"]}>
            <p>جيد جدا</p>
            <span>تقيم 154</span>
          </div>
          <div className={styles["rating-number"]}>{"8.2"}</div>
        </div>
        <div className={styles["button-container"]}>
          <button>اعرض الاسعار</button>
        </div>
      </div>
    </div>
  );
};

export default HeroSection;
